@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%
}

.responsive {
  width: 100%;
  height: 100%;
  justify-content: center;
}

.bene-image {
  width: 50%;
}

.home-page-image {
  width: 100%;
  height: 100%;
  justify-content: center;
}

.back {
  background-color: #e02525;
  display: block;
  font-weight: bold;
  color: white;
  border-radius: 0px 0px 19px 19px;
  margin-top: 3px;
}

.welcome-msg {
  text-align: center;
  color: red;
  font-size: 50px;
}

.login-msg {
  text-align: center;
  color: white;
}

.login-msg:hover {
  color: black;
}

.close-btn {
  float: right;
  color: #e74944;
}

.list {
  background: #ec008c; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fc6767,
    #ec008c
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  justify-content: center;
  width: 100%;
}

/* About*/

.about-cont {
  position: relative;
  height: 200px;
  background-image: linear-gradient(#e62211, #ff6126);
  border-bottom-left-radius: 50% 20%;
  border-bottom-right-radius: 50% 20%;
  margin: 2px;
}

.about-heading {
  color: white;
  text-align: center;
  margin: 0;
  padding: 100px 0;
  font: 44px "Arial";
}

/*list of donors*/
.donor-details > div {
  display: inline-block;
  flex-wrap: nowrap;
}

.list-wrap {
  display: "flex";
  flex-wrap: "wrap";
  justify-content: "space-around";
  overflow: "hidden";
}

/* reset */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: #2199df;
  text-decoration: unset;
}

ul {
  margin-left: 1.5rem;
  list-style: none;
}

button,
input[type="submit"] {
  border: none;
  background: none;
  color: unset;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin-top: 1em;
  margin-bottom: 1em;
}

/* general */

body {
  font-family: "Roboto", sans-serif;
}

.page {
  padding: 1rem;
}

.container {
  margin: 0 auto;
  max-width: 900px;
}

/* header */

#header {
  height: 50px;
  display: flex;

  justify-content: space-evenly;
  background-color: #e02525;
  align-items: center;
}
#header .title {
  margin: 0;
  padding: 0 1rem;
  color: #ffffff;
  font-style: oblique;
}
#header .auth-options {
  height: 100%;
  display: flex;
}
#header .auth-options button {
  padding: 0 1rem;
  display: block;
  color: #ffffff;
  font-size: 1.1rem;
  outline: none;
}
#header .auth-options button:hover {
  background-color: #be3a3a;
}

/* form */

.form label,
.form input {
  display: block;
}
.form input {
  margin-bottom: 1rem;
}
.form label {
  margin-bottom: 0.2rem;
}
.form input[type="text"],
.form input[type="email"],
.form input[type="password"] {
  width: 100%;
  padding: 0.3rem;
  border: 1px solid #c2c2c2;
  border-radius: 8px;
  background-color: #f0f0f0;
  font-size: 1.1rem;
}
.form input:focus {
  border: 1px solid #50a4e9;
  box-shadow: inset 0px 0px 5px rgba(38, 142, 228, 0.5);
  outline: none;
}
.form input[type="submit"] {
  padding: 0.3rem 0.5rem;
  border: 1px solid #6aa051;
  border-radius: 8px;
  background-color: #c8ecb7;
  font-size: 1.1rem;
}

/* error notice */

.error-notice {
  margin: 1rem 0;
  padding: 0.5rem;
  border: 1px solid #e07c7c;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8d6d6;
}
.error-notice button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #df4343;
  color: #ffffff;
}

/* media queries */

@media screen and (max-width: 550px) {
  #header .title {
    font-size: 1.2rem;
    align-self: center;
  }
  #header .auth-options button {
    padding: 0 0.5rem;
    font-size: 0.9rem;
  }
}

.icon_home {
  font-family: "Material Icons";
  font-feature-settings: "liga";
  font-weight: normal;
  font-style: normal;
  font-size: inherit;
  line-height: inherit;
  vertical-align: top;
  display: inline-block;

  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
}

